    <rubrique>Ps.118-9</rubrique>
<table id="psalm118-9">
<tr><td><rubrique>1&nbsp;</rubrique></td>
<td>Bonitátem fecísti cum servo tuo, Dómine <greStar /><br class="smallScreen" />secúndum verbum tuum.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Tu as usé de bonté envers ton serviteur, Seigneur, selon ta parole.</td>
</tr>
<tr><td><rubrique>2</rubrique></td>
<td>Bonitátem et disciplínam et sciéntiam doce me <greStar /><br class="smallScreen" />quia mandátis tuis crédidi.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Enseigne-moi le sens droit et l’intelligence, car j’ai foi en tes commandements.</td>
</tr>
<tr><td><rubrique>3</rubrique></td>
<td>Priúsquam humiliárer ego delíqui <greStar /><br class="smallScreen" />proptérea elóquium tuum custódivi.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Avant d’avoir été humilié, je m’égarais ; maintenant, j’observe ta parole.</td>
</tr>
<tr><td><rubrique>4</rubrique></td>
<td>Bonus es tu <greStar /><br class="smallScreen" />et in bonitáte tua doce me iustificatiónes tuas.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Tu es bon et bienfaisant : enseigne-moi tes lois.</td>
</tr>
<tr><td><rubrique>5</rubrique></td>
<td>Multiplicáta est super me iníquitas superbórum <greStar /><br />
ego autem in toto corde meo scrutábor mandáta tua.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Des orgueilleux imaginent contre moi des mensonges ;<br />
moi, je garde de tout cœur tes ordonnances.</td>
</tr>
<tr><td><rubrique>6</rubrique></td>
<td>Coagulátum est sicut lac cor eórum <greStar /><br class="smallScreen" />ego vero legem tuam meditátus sum.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Leur cœur est insensible comme la graisse ; moi, je fais mes délices de ta loi.</td>
</tr>
<tr><td><rubrique>7</rubrique></td>
<td>Bonum mihi quia humiliásti me <greStar /><br class="smallScreen" />ut discam iustificatiónes tuas.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Il m’est bon d’avoir été humilié, afin que j’apprenne tes préceptes.</td>
</tr>
<tr><td><rubrique>8</rubrique></td>
<td>Bonum mihi lex oris tui <greStar /><br class="smallScreen" />super míllia auri et argénti.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Mieux vaut pour moi la loi de ta bouche ; que des monceaux d’or et d’argent.</td>
</tr>
</table>
    <rubrique>Ps.118-10</rubrique>
<table id="psalm118-10">
<tr><td><rubrique>1&nbsp;</rubrique></td>
<td>Manus tuæ fecérunt me et plasmavérunt me <greStar /><br />
da mihi intelléctum et discam mandáta tua.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Ce sont tes mains qui m’ont fait et qui m’ont façonné :<br />
donne-moi l’intelligence pour apprendre tes commandements.</td>
</tr>
<tr><td><rubrique>2</rubrique></td>
<td>Qui timent te vidébunt me et lætabúntur <greStar /><br />
quia in verba tua supersperávi.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Ceux qui te craignent, en me voyant, se réjouiront,<br />
car j’ai confiance en ta parole.</td>
</tr>
<tr><td><rubrique>3</rubrique></td>
<td>Cognóvi, Dómine, quia ǽquitas iudícia tua <greStar /><br />et in veritáte humiliásti me.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Je sais, Seigneur, que tes jugements sont justes ;<br />
c’est dans ta fidélité que tu m’as humilié.</td>
</tr>
<tr><td><rubrique>4</rubrique></td>
<td>Fiat misericórdia tua ut consolétur me <greStar /><br class="smallScreen" />secúndum elóquium tuum servo tuo.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Que ta bonté soit ma consolation, selon ta parole donnée à ton serviteur.</td>
</tr>
<tr><td><rubrique>5</rubrique></td>
<td>Véniant mihi miseratiónes tuæ et vivam <greStar /><br class="smallScreen" />quia lex tua meditátio mea est.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Que ta compassion vienne sur moi, et que je vive, car ta loi fait mes délices.</td>
</tr>
<tr><td><rubrique>6</rubrique></td>
<td>Confundántur supérbi quia iniúste iniquitátem fecérunt in me <greStar /><br />
