<tr><td><rubrique>8</rubrique></td>
<td>Fiat cor meum immaculátum in iustificatiónibus tuis <greStar /><br class="smallScreen" />ut non confúndar.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Que mon cœur soit tout entier à tes lois, afin que je ne sois pas confondu.</td>
</tr>
</table>
    <rubrique>Ps.118-11</rubrique>
<table id="psalm118-11">
<tr><td><rubrique>1&nbsp;</rubrique></td>
<td>Defécit in salutáre tuum ánima mea <greStar /><br class="smallScreen" />et in verbum tuum supersperávi.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Mon âme languit après ton salut, j’espère en ta parole.</td>
</tr>
<tr><td><rubrique>2</rubrique></td>
<td>Defecérunt óculi mei in elóquium tuum <greStar /><br class="smallScreen" />
dicéntes quando consoláberis me ?</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Mes yeux languissent après ta promesse, je dis : Quand me consoleras-tu ?</td>
</tr>
<tr><td><rubrique>3</rubrique></td>
<td>Quia factus sum sicut uter in pruína <greStar /><br class="smallScreen" />iustificatiónes tuas non sum oblítus.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Car je suis comme une outre exposée à la fumée, mais je n’oublie pas tes lois.</td>
</tr>
<tr><td><rubrique>4</rubrique></td>
<td>Quot sunt dies servi tui ? <greStar /><br />
