<tr><td><rubrique>5</rubrique></td>
<td>Narravérunt mihi iníqui fabulatiónes <greStar /><br />sed non ut lex tua.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Des orgueilleux creusent des fosses pour me perdre ;<br />
