</tr>
<tr><td><rubrique>6</rubrique></td>
<td>Ómnia mandáta tua véritas <greStar /><br />iníque persecúti sunt me, ádiuva me.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Tous tes commandements sont fidélité ;<br />
ils me persécutent sans cause : secours-moi.</td>
</tr>
<tr><td><rubrique>7</rubrique></td>
<td>Paulo minus consummavérunt me in terra <greStar /><br />ego autem non derelíqui mandáta tua.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Ils ont failli m’anéantir dans le pays ;<br />
et moi je n’abandonne pas tes ordonnances.</td>
</tr>
<tr><td><rubrique>8</rubrique></td>
<td>Secúndum misericórdiam tuam vivífica me <greStar /><br class="smallScreen" />et custódiam testimónia oris tui.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Rends-moi la vie dans ta bonté, et j’observerai l’enseignement de ta bouche.</td>
</tr>
</table>
    <rubrique>Ps.118-12</rubrique>
<table id="psalm118-12">
<tr><td><rubrique>1&nbsp;</rubrique></td>
<td>In ætérnum, Dómine <greStar /><br class="smallScreen" />verbum tuum pérmanet in cælo.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>A jamais, Seigneur, ta parole est établie dans les cieux.</td>
</tr>
<tr><td><rubrique>2</rubrique></td>
<td>In generatiónem et generatiónem véritas tua <greStar /><br class="smallScreen" />fundásti terram et pérmanet.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>D’âge en âge ta fidélité demeure ; tu as fondé la terre, et elle subsiste.</td>
</tr>
<tr><td><rubrique>3</rubrique></td>
<td>Ordinatióne tua persevérat dies <greStar /><br />
