</tr>
<tr><td><rubrique>4</rubrique></td>
<td>Nisi quod lex tua meditátio mea est <greStar /><br class="smallScreen" />tunc forte periíssem in humilitáte mea.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Si ta loi ne faisait mes délices, déjà j’aurais péri dans ma misère.</td>
</tr>
<tr><td><rubrique>5</rubrique></td>
<td>In ætérnum non oblivíscar iustificatiónes tuas <greStar /><br />
