</tr>
<tr><td><rubrique>6</rubrique></td>
<td>Tuus sum ego, salvum me fac <greStar /><br class="smallScreen" />quóniam iustificatiónes tuas exquisívi.</td></tr>
<tr class="trad"><td>&nbsp;</td>
<td>Je suis à toi : sauve-moi, car je recherche tes préceptes.</td>
</tr>
<tr><td><rubrique>7</rubrique></td>
<td>Me exspectavérunt peccatóres ut pérderent me <greStar /><br />
